import React from 'react';
import '../style/PrivacyPolicy.css'; // Import the CSS styles

const PrivacyPolicy = () => {
  return (
    <div className="privacy-policy-container">
      <h1>Privacy Policy</h1>
      <p><strong>Last Updated:</strong> September, 2024</p>
      <p>ATAKY ("we," "us," or "our") is committed to protecting your privacy. This Privacy Policy explains how we collect, use, disclose, and safeguard your information when you visit our website <a href="www.ataky.com" target="_blank" rel="noopener noreferrer">www.ataky.com</a>. By using our Site, you consent to the practices described in this Privacy Policy.</p>
      <h2>1. Information We Collect</h2>
      <p><strong>Personal Information:</strong> We collect personal information you provide directly to us, such as your name, email address, phone number, and shipping address when you create an account, place an order, or contact us.</p>
      <p><strong>Payment Information:</strong> We collect payment information necessary to process your transactions, such as credit card numbers and billing addresses.</p>
      <p><strong>Usage Data:</strong> We collect information about your interactions with our Site, including IP address, browser type, pages visited, and referral information.</p>
      <h2>2. How We Use Your Information</h2>
      <p><strong>To Process Orders:</strong> We use your information to process and fulfill orders, handle returns and exchanges, and provide customer support.</p>
      <p><strong>To Improve Our Services:</strong> We use your information to analyze Site usage, improve our services, and customize your experience.</p>
      <p><strong>To Communicate with You:</strong> We use your contact information to send you order confirmations, shipping updates, promotional offers, and other relevant information. You may opt-out of promotional communications at any time.</p>
      <h2>3. How We Share Your Information</h2>
      <p><strong>Service Providers:</strong> We may share your information with third-party service providers who assist us in operating our Site, processing payments, and delivering orders. These providers are obligated to protect your information and use it only for the purposes we specify.</p>
      <p><strong>Legal Requirements:</strong> We may disclose your information to comply with legal obligations, enforce our Terms, or protect the rights, property, or safety of ATAKY, our customers, or others.</p>
      <h2>4. Security</h2>
      <p><strong>Data Protection:</strong> We implement reasonable security measures to protect your personal information from unauthorized access, use, or disclosure. However, no method of transmission over the internet or electronic storage is completely secure.</p>
      <h2>5. Changes to Privacy Policy</h2>
      <p><strong>Policy Updates:</strong> We may update or modify this Privacy Policy from time to time. Any changes will be effective immediately upon posting on our Site. Your continued use of our Site constitutes acceptance of the updated Privacy Policy.</p>
    </div>
  );
}

export default PrivacyPolicy;